#reviews {
    margin-top: 190px;

    .container {
        width: 1060px;
    }

    #review-left {
        width: 50%;
        height: 584px;
        display: inline-block;
        vertical-align: top;
        position: relative;
        top: -30px;

        & > div {
            margin: 0 80px 0 46px;
        }

        #review-quote {
            margin-left: 7px !important;
            width: 82px;
            height: 82px;
            text-align: center;
            -webkit-border-radius: 3px;
            -moz-border-radius: 3px;
            border-radius: 3px;
            -webkit-box-shadow: -20px 18px 44px -8px rgba(0,0,0,0.22);
            -moz-box-shadow: -20px 18px 44px -8px rgba(0,0,0,0.22);
            box-shadow: -20px 18px 44px -8px rgba(0, 0, 0, 0.22);

            p {
                padding-top: 5px;
                font-size: 118px;
                font-family: 'Palanquin', sans-serif;
                color: $paleBrown;
            }
        }

        #review-text {
            font-size: 16px;
            font-style: italic;
            color: #868686;
            line-height: 2.1;
        }

        #review-date {
            font-size: 16px;
            color: $offBlack;
            margin-top: 20px;
        }

        #reviewer {
            margin-top: 56px !important;

            #reviewer-img {
                display: inline-block;

                img {
                    height: 66px;
                    width: 66px;
                    -webkit-border-radius: 50%;
                    -moz-border-radius: 50%;
                    border-radius: 50%;
                }
            }

            #reviewer-name-title {
                display: inline-block;
                position: relative;
                bottom: 10px;
                left: 20px;

                p {
                    margin-top: 0;

                    &:first-child {
                        color: $offBlack;
                        font-size: 17px;
                        font-weight: bold;
                    }

                    &:last-child {
                        color: #868686;
                        margin-top: 14px;
                        margin-bottom: 10px;
                    }
                }
            }
        }

        #review-nav {
            margin-left: 32px !important;
            position: relative;
            top: 98px;
        }
    }

    #review-right {
        width: 49%;
        display: inline-block;
        text-align: center;

        img {
            width: 490px;
            height: 584px;
        }
    }
}
