@import "reset";
@import "variables";
@import "landing/top";
@import "landing/search";
@import "landing/featured";
@import "landing/reasons";
@import "landing/weekly_house";
@import "landing/call2action";
@import "landing/reviews";
@import "landing/contact";
@import "landing/bottom";
@import "landing/copyright_credits";
@import url('https://fonts.googleapis.com/css?family=Poppins:200,500&display=swap');
@import url('https://fonts.googleapis.com/css?family=Palanquin&display=swap');

body, a, ::placeholder {
    font-family: 'Poppins', sans-serif;
    color: $offBlack;
}

section {
    position: relative;
    width: 100%;
}

.container {
    width: 80%;
    margin: 0 auto;
    position: relative;
}

h2 {
    color: $offBlack;
    font-size: 32px;
    letter-spacing: -1px;
    font-weight: bold;
}

p {
    margin-top: 32px;
    font-size: 14px;
    color: $lightGray;
}

.soft-shadow {
    -webkit-box-shadow: 0px 12px 63px 2px rgba(0,0,0,0.24);
    -moz-box-shadow: 0px 12px 63px 2px rgba(0,0,0,0.24);
    box-shadow: 0px 12px 63px 2px rgba(0,0,0,0.24);
}

.soft-drop-shadow {
    -webkit-box-shadow: 0px 35px 63px 6px rgba(0, 0, 0, 0.06);
    -moz-box-shadow: 0px 35px 63px 6px rgba(0, 0, 0, 0.06);
    box-shadow: 0px 35px 63px 6px rgba(0, 0, 0, 0.06);
}

.right-arrow {
    margin-left: 40px;
    margin-right: 7px;
}

.left-arrow {
    i {
        padding: 10px 5px 10px 15px;
    }
}

.right-arrow {
    i {
        padding: 10px 5px;
    }
}

.fa-long-arrow-left, .fa-long-arrow-right {
    color: $offWhite;
    transition: 0.1s ease-in-out;

    &:hover {
        color: $paleBrown;
    }
}

.fa-long-arrow-left {
    -webkit-transform:scale(1,1.3); /* Safari and Chrome */
    -moz-transform:scale(1,1.3); /* Firefox */
    -ms-transform:scale(1,1.3); /* IE 9 */
    -o-transform:scale(1,1.3); /* Opera */
    transform:scale(1,1.3); /* W3C */
}

.fa-long-arrow-right {
    -webkit-transform:scale(3,1.2); /* Safari and Chrome */
    -moz-transform:scale(3,1.2); /* Firefox */
    -ms-transform:scale(3,1.2); /* IE 9 */
    -o-transform:scale(3,1.2); /* Opera */
    transform:scale(3,1.2); /* W3C */
}

@media only screen and (min-width: 240px) and (max-width: 767px) {
    #top {
        height: 630px;

        #logo-small {
            padding-top: 25px;
        }

        #navigation {
            padding-top: 17px;

            #hamburger {
                display: block;
            }

            #links {
                ul {
                    li {
                        display: block;
                        visibility: hidden;
                        opacity: 0;
                        margin-top: 16px;
                        font-size: 16px;
                        padding-right: 0;
                        text-align: right;
                        transition: 0.2s ease-in-out;
                    }
                }
                &.change ul {
                    li {
                        visibility: visible;
                        opacity: 1;
                    }
                }
            }

            #signup {
                display: none;
            }
        }

        #opening {
            #statement {
                width: 210px;
                font-size: 45px;
                letter-spacing: -3px;
            }

            #description {
                width: 100%;
                font-size: 12px;
                margin-top: 14px;
                line-height: 25px;
            }
        }
    }

    #search {
        top: -70px;

        .container {
            width: 90%;
        }

        #general {
            li {
                a {
                    padding: 9px 21px;
                }
            }
        }

        #specifics {
            margin-top: 14px;
            padding: 16px 26px;
            width: 80%;
            max-width: 375px;
            display: block;
            position: relative;

            #location, #type, #price {
                text-align: center;
            }

            #type, #price {
                margin-top: 25px;
            }

            #search-button {
                div {
                    padding: 8px;
                    margin: 25px auto 5px auto;
                    width: 80%;
                }

                i {
                    font-size: 16px;
                }
            }

            .filter-type {
                font-size: 16px;
            }

            .filter-dropdown {
                font-size: 16px;
            }

            .filter-value {
                font-size: 20px;
                text-align: center;
            }
        }
    }

    #featured {
        margin-top: -40px;

        .container:last-child {
            margin-top: 10px;
            width: 90%;
        }

        #featured-menu {
            width: 80%;
            max-width: 400px;
            grid-template-columns: unset;
            grid-template-rows: 1fr 1fr 1fr;

            div {
                &:nth-child(2) {
                    grid-row: 1 / 2;
                }

                margin-bottom: 22px;
            }
        }

        #featured-items {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 1fr;
            row-gap: 14px;
            column-gap: 14px;
            margin: 10px auto 0 auto;
            align-items: start;

            .featured-item {
                width: 100%;
                margin: 0;
            }

            .featured-item-img {
                width: 100%;
                height: 250px;
            }

            .featured-item-details {
                font-size: 13px;
                .featured-item-rooms, .featured-items-baths {
                    padding-right: 5%;
                }

                .featured-item-baths {
                    padding-left: 10px;
                }

                .featured-item-price {
                    padding-left: unset;
                    border-left: unset;
                    display: block;
                    margin-top: 10px;
                }
            }
        }

        #featured-navigation {
            margin-top: 10px;
            position: relative;
            right: 5px;
        }
    }

    #reasons {
        .container {
            width: 95%;
        }

        #reasons-details {
            border-bottom: unset;

            .reasons-details-container {
                margin-top: 5px;

                &:nth-child(2) {
                    margin: 5px 0 0 0;
                }
            }
        }
    }

    #weekly-house {
        margin-top: 50px;
        height: 480px;

        #weekly-house-highlight {
            div {
                margin: 15px 15px;

                h2 {
                    font-size: 20px;
                }

                p {
                    font-size: 13px;
                    margin-top: 5px;
                    line-height: 1.0;
                }
            }
        }

        #weekly-house-tour-button {
            width: 158px;
            height: 132px;
            margin: -65px auto;

            div {
                &:nth-child(1) {
                    top: 20px;
                    left: 50px;
                }

                &:nth-child(2) {
                    top: 95px;
                    left: 30px;
                }
            }
        }
    }

    #call-to-action {
        margin-top: 85px;

        .container {
            width: 90%;

            #call-to-action-left {
                width: 100%;
                height: 480px;

                #c2a-left-dialog {
                    left: 0;
                    bottom: 0;
                    z-index: unset;
                }
            }

            #call-to-action-right {
                width: 100%;
                height: 480px;
                margin-top: 50px;
                margin-left: 0;

                #c2a-right-dialog {
                    left: 0;
                    top: unset;
                    bottom: 0;
                }
            }

            .c2a-dialog {
                width: 100%;
                height: unset;
                padding: 0;
                -webkit-border-radius: unset;
                -moz-border-radius: unset;
                border-radius: unset;

                div:last-child {
                    margin: 10px 0;
                    padding: 15px 0;
                }

                .c2a-text {
                    font-size: 16px;
                    margin: 10px 10px 10px 15px;
                }

                .c2a-link {
                    font-size: 14px;
                    margin-right: 30px;
                }
            }
        }
    }

    #reviews {
        .container {
            width: 90%;
            top: -165px;
        }

        #review-left {
            width: unset;
            height: 480px;

            & > div {
                margin: unset;
            }

            #review-quote {
                width: 65px;
                height: 65px;

                p {
                    font-size: 100px;
                }
            }

            #review-text {
                font-size: 14px;
                line-height: 1.9;
                margin-top: 15px;
            }

            #reviewer {
                margin-top: 27px !important;

                #reviewer-name-title {
                    left: 10px;

                    p {
                        &:last-child {
                            font-size: 12px;
                        }
                    }
                }
            }

            #review-nav {
                margin-left: unset !important;
                top: unset;
            }
        }

        #review-right {
            display: none;
        }
    }

    #contact {
        margin-top: -160px;

        .container {
            width: 100%;
        }

        #contact-form {
            width: 70%;
            height: 215px;
            padding: 24px 30px 14px 30px;

            form {
                textarea,
                input {
                    font-size: 18px;
                }
            }
        }

        #contact-background {
            img {
                width: 100%;
            }
        }
    }

    #bottom {
        .container {
            padding-top: 40px;
            width: 95%;
        }

        #logo-social {
            top: unset;
            left: 7px;
            text-align: center;

            #logo-large {
                span {
                    left: unset;
                }
            }

            #social-media {
                margin-top: 20px;
            }
        }

        #bottom-navigation {
            margin: 50px auto 0 auto;
            width: 80%;
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 1fr;
            column-gap: 20px;
            row-gap: 30px;
            padding-left: 15%;
            max-width: 400px;

            & > div {
                margin-right: 0;
            }

            div {
                ul {
                    li {
                        font-size: 10px;
                    }
                }
            }
        }

        #subscribe {
            margin-top: 30px;
            text-align: center;
        }
    }
}

@media only screen and (min-width: 425px) and (max-width: 767px) {
    #top {
        #opening {
            #statement {
                width: 260px;
                font-size: 55px;
                letter-spacing: -3px;
            }
        }
    }

    #weekly-house {
        #weekly-house-highlight {
            div {
                margin: 20px 20px;

                h2 {
                    font-size: 20px;
                }

                p {
                    font-size: 14px;
                    margin-top: 5px;
                    line-height: 1.6;
                }
            }
        }
    }
}

@media only screen and (min-width: 768px) and (max-width: 852px) {
    #top {
        #navigation {
            #links {
                ul {
                    li {
                        padding-right: 20px;
                    }
                }
            }
        }
    }

    #search {
        .container {
            width: 90%;
        }
    }

    #reasons {
        #reasons-details {
            border-bottom: unset;
        }
    }
}

@media only screen and (min-width: 768px) and (max-width: 1199px) {
    #reasons {
        .container {
            width: 95%;
        }

        #reasons-details {
            .reasons-details-container {
                margin-top: 40px;

                &:nth-child(2) {
                    margin: 40px 0 0 0;
                }
            }
        }
    }

    #weekly-house {
        margin-top: 75px;

        #weekly-house-highlight {
            div {
                margin: 50px 80px;
            }
        }
    }

    #reviews {
        .container {
            width: 90%;
            top: -30px;
        }

        #review-left {
            & > div {
                margin: 0 10px 0 0;
            }
        }

        #review-right {
            top: 85px;

            img {
                width: 100%;
            }
        }
    }

    #contact {
        #contact-background {
            img {
                width: 100%;
                max-width: 820px;
            }
        }
    }

    #bottom {
        .container {
            width: 90%;
        }

        #logo-social {
            top: 30px;
        }

        #bottom-navigation {
            margin: 0 auto 0 20px;
            display: inline-flex;
            align-content: space-between;
            top: -104px;

            & > div {
                margin-right: 20px;
            }
        }

        #subscribe {
            display: none;
        }
    }
}

@media only screen and (min-width: 768px) and (max-width: 1328px) {
    #call-to-action {
        margin-top: 85px;

        .container {
            width: 90%;
            display: grid;
            grid-template-columns: 1fr 1.5fr;
            column-gap: 15px;
            align-items: end;

            #call-to-action-left {
                width: 100%;

                #c2a-left-dialog {
                    width: 85%;
                    left: 15%;
                    bottom: -40px;
                }
            }

            #call-to-action-right {
                width: 100%;
                margin-top: 23px;
                margin-left: 0;

                #c2a-right-dialog {
                    left: 20%;
                }
            }

            .c2a-dialog {
                width: 50%;
                max-width: 320px;
                padding: 15px 23px 0 23px;

                .c2a-text {
                    font-size: 18px;
                }

                .c2a-link {
                    font-size: 14px;
                }
            }
        }
    }
}

@media only screen and (min-width: 768px) and (max-width: 1439px) {
    #featured {
        .container:first-child {
            width: 95%;
        }

        .container:last-child {
            width: 90%;
        }

        #featured-items {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            column-gap: 14px;

            .featured-item {
                min-width: 150px;
                width: 100%;
            }

            .featured-item-details {
                .featured-item-price {
                    padding-left: unset;
                    border-left: unset;
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }

    #bottom {
        .container {
            width: 90%;
            padding-bottom: unset;
            height: 215px;
        }

        #logo-social {
            top: 30px;
        }

        #bottom-navigation {
            margin: 0 auto 0 20px;
            display: inline-flex;
            align-content: space-between;
            top: -104px;

            & > div {
                margin-right: 20px;
            }
        }
    }
}